<template>
    <div >
        <input type="file" id="upload-ipt" @change="chooseVideo($event,'vId')" accept="video/*" />
        <div id="vId" style="width: 500px;height: 300px;border:1px solid #ccc;"></div>
        <div>第一帧图片展示</div>
        <div style="width: 500px;height: auto">
            <img :src="firstImgUrl" width="100%" />
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                firstImgUrl:''
            }
        },
        mounted(){

        },
        methods:{
            chooseVideo(e,eleId) {
                let self = this;
                console.log(e);
                let files = e.target.files[0];
                let blob = new Blob([files]); // 文件转化成二进制文件
                let url = URL.createObjectURL(blob); //转化成url

                let parent = document.getElementById(eleId);
                let div = document.createElement("div");
                div.setAttribute("class", "newDivVideo");
                parent.appendChild(div);
                document.querySelector('.newDivVideo').innerHTML = '<video controls src="'+url+'" id="videoId" width="100%" height="100%" autoplay></video>';
                let videoElement = document.querySelector('#videoId');
                videoElement.addEventListener("canplay", function() {
                    let canvas = document.createElement("canvas");
                    canvas.width = 500;
                    canvas.height = 300;
                    canvas.getContext("2d").drawImage(videoElement, 0, 0, canvas.width, canvas.height);
                    let outImgUrl = canvas.toDataURL("image/png");
                    console.log(outImgUrl); //第一帧图片url
                    self.firstImgUrl = outImgUrl;
                })
            }
        },
        components:{

        }
    }
</script>
<style scoped>

</style>



